import { ImageResponse } from 'next/og';
export const size = { width: 1200, height: 630 };
export const contentType = 'image/png';

export default async function OG() {
    return new ImageResponse(
        (
            <div style={{
                height: '100%', width: '100%', display: 'flex',
                background: 'linear-gradient(135deg,#0a0a0f,#0b0d15)', color: 'white',
                justifyContent: 'center', alignItems: 'center', fontSize: 56
            }}>
                <div style={{ display:'flex', gap: 24, alignItems:'center' }}>
                    <div style={{ width: 140, height: 140, borderRadius: 9999,
                        background: 'radial-gradient(circle at 30% 30%, #6ee7f9 0%, #a855f7 60%, transparent 70%)' }} />
                    <div style={{ display:'flex', flexDirection:'column' }}>
                        <b>Cybernith</b>
                        <span style={{ fontSize: 28, opacity:.85 }}>Full-Stack Developer · Django/Go · Vue/React</span>
                    </div>
                </div>
            </div>
        ),
        size
    );
}
